<template>
  <el-form label-width="120px" :model="personForm" :rules="rules" :inline="true" ref="personForm">
    <el-form-item label="机构" prop="org">
      <el-select v-model="personForm.org" placeholder="请选择机构" clearable>
        <el-option v-for="item in personFormData.orgList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="渠道" prop="channel">
      <el-select v-model="personForm.channel" placeholder="请选择渠道" clearable>
        <el-option v-for="item in personFormData.channelList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="配费模式" prop="feeModel">
      <el-select v-model="personForm.feeModel" placeholder="请选择配费模式" clearable>
        <el-option v-for="item in personFormData.feeModelList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="统计截至月份" prop="endMonth" v-if="endMonthShow">
      <el-select v-model="personForm.endMonth" placeholder="请选择统计截至月份" clearable>
        <el-option v-for="item in personFormData.monthList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算期间" prop="timeRange" v-if="timeRangeShow">
      <el-select v-model="personForm.timeRange" placeholder="请选择预算期间" clearable>
        <el-option v-for="item in personFormData.timeRangeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="版本号" prop="version" v-if="versionShow">
      <el-input v-model="personForm.version"></el-input>
    </el-form-item>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'VersionQueryForm',
  props: {
    personForm: {
      type: Object,
      required: true
    },
    rules: {
      type: Object,
      default: null
    },
    endMonthShow: {
      type: Boolean,
      required: true
    },
    timeRangeShow: {
      type: Boolean,
      required: true
    },
    versionShow: {
      type: Boolean,
      required: true
    }
  },
  created () {
    this.$store.dispatch('initPersonFormData')
  },
  computed: {
    ...mapState({
      personFormData: state => state.inspect.personFormData
    })
  }
}
</script>

<style scoped>

</style>



